.boton-contenedor{
    height: 83px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background-color: #1b1b32;
    color: white;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    border: 2px solid white;
    margin: 4px;
    cursor: pointer;
    user-select: none;
    flex: 1 1;
    transition: .2s;
    -webkit-transition: .2s;
    -moz-transition: .2s;
    -ms-transition: .2s;
    -o-transition: .2s;
}

.boton-contenedor:hover{

    background-color: rgb(1, 1, 255);
}

.operador{
    background-color: #895575;
}

.operador:hover{
    background-color:darkorange;
}