.App {
  background-color: #1b1b32;
  width: 100%;
  height: 120vh;
  padding-top:.6rem ;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}
.freecodecamp-logo-contenedor{
  
  height: 3.125rem;
  margin: .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.freecodecamp-logo{
  height: 3rem;
}

.contenedor-calculadora{
  width: 25rem;
  min-height: 38rem;
  padding: 0.875rem;
  background-color:#9754cb;
  border-radius: .5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  border: 0.313rem solid white;

}

.fila{
  margin:.25rem ;
  display: flex;
  align-items: center;
  justify-content: center;
}


@media only screen and (max-width:768px) {

  .freecodecamp-logo{
    height: 2rem;
  }
  .contenedor-calculadora{
    width: 20rem;
    height: 32.5rem;
    
  }
}