.boton-clear{
    min-width: 100%;
    height: 3rem;
    border-radius: .5rem;
    background-color: blue;
    margin: 1rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
    transition: .2s;
    -webkit-transition: .2s;
    -moz-transition: .2s;
    -ms-transition: .2s;
    -o-transition: .2s;
    cursor: pointer;
}
.boton-clear:hover{
    background-color: green;
    color: yellow;
}