.input{
    border-style: solid;
    max-width: 100%;
    height: 75px;
    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    background-color: black;
    color: white;
    padding: 11px 30px 11px 11px;
    margin: 1px solid #888 ;
    box-shadow: 0 0 .5rem white ;
    
}